import React, { useState, useEffect } from "react";
import { useParams } from "react-router";
import axios from "axios";
import theme from "../../styles/theme";
import { DetailedContent,JamButton } from "./styled";
import moment from "moment";
import { useNavigate, Navigate } from "react-router-dom";
import { decryptData } from "../../Crypted";

const DisplayperStudents = () => {
  const [data, setData] = useState({activities:[]});
  const params = useParams();
  const history = useNavigate();
  let mkLocalData = localStorage.getItem("mk");
  const salt = "6d090796-ecdf-11ea-adc1-0242ac120003";
  const originalData = decryptData(mkLocalData, salt);
  useEffect(() => {
    const fetchData = async () => {
      const response = await axios.get(
        `https://gui-kel-1.herokuapp.com/profiles/${params.id}`
      );
      setData(response.data.data.profiles);
    };
    fetchData();
    if (originalData !== null) {
      if (
        originalData.email === "admin@gmail.com" &&
        originalData.pw === "adminkel1ati"
      )
        console.log("halo");
      else history("/web-gui-kel-1");
    }
  }, [params.id, data, history, originalData]);

  return (
    <>
      {originalData === null && data.length === 0 ? (
        <Navigate to="/web-gui-kel-1" />
      ) : (
        <>
          <h1
            style={{
              textAlign: "center",
              margin: "60px auto 10px",
              color: theme.color.blue.A700,
            }}
          >
            Detailed Mahasiswa
          </h1>
          <div style={{ height: "100%", width: "80%", margin: "80px auto" }}>

            <h6 className="bold" style={{ marginBottom: "10px" }}>
              <span style={{ color: theme.color.green.A800 }}>Nama : </span>{" "}
              {data.name}
            </h6>

            <h6 className="bold" style={{ marginBottom: "10px" }}>
              <span style={{ color: theme.color.green.A800 }}>NPM :</span>{" "}
              {data.npm}
            </h6>
            {data.activities.map((item, idx) => {
              return (
                <DetailedContent key={idx}>
                  <JamButton last={idx !== data.activities.length-1} />
                  <div style={{ flex: 1 }}>
                    <h6 style={{ marginBottom: "-12px" }}>
                      {moment(data.updatedAt[idx]).format(
                        "DD-MM-YYYY, h:mm:ss a"
                      )}
                    </h6>
                    <h6 className="bold" style={{ marginBottom: "10px" }}>
                      {item}
                    </h6>
                  </div>
                </DetailedContent>
              );
            })}
          </div>
        </>
      )}
    </>
  );
};

export default DisplayperStudents;
